<template>
  <div class="calc">
    <div class="calc__display">
      <span class="calc__text"> {{ current || 0 }} </span>
    </div>
    <button class="calc__button calc__button--operation" @click="clear">C</button>
    <button class="calc__button calc__button--operation" @click="sign">+/-</button>
    <button class="calc__button calc__button--operation" @click="percent">%</button>
    <button class="calc__button calc__button--operation" @click="divide">/</button>
    <button class="calc__button" @click="append('7')">7</button>
    <button class="calc__button" @click="append('8')">8</button>
    <button class="calc__button" @click="append('9')">9</button>
    <button class="calc__button calc__button--operation" @click="multiply">*</button>
    <button class="calc__button" @click="append('4')">4</button>
    <button class="calc__button" @click="append('5')">5</button>
    <button class="calc__button" @click="append('6')">6</button>
    <button class="calc__button calc__button--operation" @click="minus">-</button>
    <button class="calc__button" @click="append('1')">1</button>
    <button class="calc__button" @click="append('2')">2</button>
    <button class="calc__button" @click="append('3')">3</button>
    <button class="calc__button calc__button--operation" @click="plus">+</button>
    <button class="calc__button calc__button--zero" @click="append('0')">0</button>
    <button class="calc__button calc__button--dot" @click="dot">.</button>
    <button class="calc__button calc__button--operation" @click="equal">=</button>
    <pre>{{ $data }}</pre>
  </div>
</template>

<script>
export default {
  name: 'calc',
  data () {
    return {
      current : "",
      operator: "",
      operatorClicked: false,
      previousNumber: null,
      computedState: false,
      computedA: null,
      computedB: null,
      operatorInited: false
    }
  },
  methods: {
    clear: function () {
      this.current = "";
      this.operator = "";
      this.previousNumber = null;
      this.computedState = false;
      this.computedA = null;
      this.computedB = null;
      this.operatorClicked = false;
      this.operatorInited = false;

    },

    sign: function () {
      if (this.current !== "") {
        this.current = (this.current.charAt(0) === "-") ? this.current.slice(1) : `-${this.current}`;
      }
    },

    percent: function () {
      this.current = `${parseFloat((this.current / 100).toFixed(8))}`;
      this.operatorClicked = true
    },

    append: function (number) {
      if (this.computedState) {
        this.clear()
      }
      if (
        number !== '0' || ( number === '0' && this.current !== '' && this.current !== '0')
      ) {
        if (this.operatorClicked){
          this.current = `${number}`;
          this.operatorClicked = false;
        } else {
          this.current = `${this.current}${number}`;
        }
      }
    },

    dot: function () {
      if (
        this.current === '' || this.operatorClicked
      ) {
        this.current = "0.";
        this.operatorClicked = false;
        this.computedState = false;

      } else if (
        this.current.indexOf('.') === -1
      ) {
        this.append(".")
      }
    },

    preoperate: function () {
      if (this.operatorInited) {
        this.equal();
      }
      this.computedState = false;
      this.previousNumber = this.current;
      this.operatorClicked = true;
      this.operatorInited = true;
    },

    plus: function (){
      this.preoperate();
      this.operator = "+"
    },
    minus: function (){
      this.preoperate();
      this.operator = "-"
    },
    multiply: function (){
      this.preoperate();
      this.operator = "*"
    },
    divide: function (){
      this.preoperate();
      this.operator = "/"
    },

    operate: function (operator, a, b) {
      switch (operator) {
        case "+":
          return a + b;
        case "-":
          return a - b;
        case "*":
          return a * b;
        case "/":
          return a / b;
      }
    },

    equal: function () {
      if (this.previousNumber && this.current && this.operator) {
        this.computedA = (this.computedState) ? this.current : this.previousNumber;
        this.computedB = (this.computedState) ? this.computedB : this.current;
        this.current = `${parseFloat(this.operate(
          this.operator,
          parseFloat(this.computedA),
          parseFloat(this.computedB)
        ).toFixed(8))}`;
        this.computedState = true;
        this.operatorClicked = true;
        this.operatorInited = false;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
  pre
    grid-column-start: span 4
    grid-row-start: span 2
    color: #6f7888
    text-align: left
  .calc
    width: 400px
    margin: auto
    margin-bottom: 20px
    padding: 10px
    display: grid
    grid-template-columns: repeat(4, 1fr)
    grid-auto-rows: minmax(50px, auto)
    grid-gap: 5px
    background-color: #052828
    &__display
      grid-column-start: span 4
      grid-row-start: span 2
      background-color: #ddd
      display: flex
      align-items: center
      justify-content: center
    &__button
      border: 1px solid #555
      border-radius: 5px
      background-color: #0D4764
      font-size: 20px
      color: #ddd
      &--zero
        grid-column-start: span 2
      &--operation
        color: #fff
        background-color: #001B28

</style>



