<template>
  <header class="header">
    <div class="logo">
      <a href="#" class="logo__link" @click.prevent="inputChanged">
        <img src="../assets/logo.png" alt="" class="logo__img">
      </a>
    </div>
  </header>
</template>

<script>
  export default {
  name: 'vheader',
  data () {
    return {
      asideStatus: false
    }
  },
  methods: {
    inputChanged () {
      this.asideStatus = !this.asideStatus;
      this.$root.$emit('eventing', this.asideStatus);
    }
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
  .header
    display: flex
    justify-content: center
    align-items: center
    position: fixed
    top: 0
    left: 0
    width: 100%
    height: 80px
    background-color: rgba(35, 37, 38, 0.5)
  .logo
    position: relative
    &, &__link, &__img
      height: 60px
      display: block
    &:before
      content: 'Кликай! ->'
      position: absolute
      top: 50%
      left: 0
      width: auto
      transform: translate(-150px, -50%)
      color: #a8b9d2

</style>



