<template>
  <transition name="asideToggle">
    <aside class="aside" v-if="show">
      <ul class="aside__list">
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">Calculators</a>
        </li>
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">Header</a>
        </li>
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">Sidebar</a>
        </li>
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">And main container</a>
        </li>
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">Are different onefile Vue components.</a>
        </li>
        <li class="aside__item">
          <a href="#" class="aside__link" @click.prevent="">with crosscomponent events</a>
        </li>
      </ul>
    </aside>
  </transition>
</template>

<script>

  export default {
    name: 'sidebar',
    data () {
      return {
        show: false
      }
    },
    mounted() {
        this.$root.$on('eventing', data => {
            this.show = data;
        });
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
  .aside
    position: fixed
    top: 80px
    left: 0
    bottom: 0
    width: 300px
    padding: 10px
    border-top: 1px solid rgb(35, 37, 38)
    background: rgba(35, 37, 38, 0.5)
    &__list
      margin: 0
      padding: 0
    &__item
      list-style: none
      padding: 0
    &__link 
      text-decoration: none
      font-size: 18px
      color: #a8b9d2

  .asideToggle-enter-active, .asideToggle-leave-active
    transition: opacity .5s

  .asideToggle-enter, .asideToggle-leave-to
    opacity: 0

</style>



